* {
  margin: 0;
  padding: 0;
}

.box {
  width: 100%;
  height: 100vh;
  background: #ccc;
}

.couponslider-item {
  display: inline-block;
  vertical-align: middle;
}

.couponslider-right {
  width: 630px;
  height: 360px;
  background-size: 100% 100%;
  background-position: cover;
  background-color: #12cea4;
}

.couponslider-left {
  width: 50px;
  height: 360px;
  background-color: #333;
  background-image: url("https://cdn.shopify.com/s/files/1/0118/7659/7856/files/bg-couponslider2.png?v=1624883905");
  background-size: 100% 100%;
  background-position: cover;
  cursor: pointer;
}

.couponslider-box {
  width: 680px;
  font-size: 0;
  position: fixed;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  transition: right .2s;
  z-index: 99;
}

.couponslider-box-hide {
  right: -630px;
}

.couponslider-box svg {
  width: 100%;
}

.couponslider-subscribe {
  text-align: center;
  padding-top: 66px;
}

.couponslider-subscribe svg {
  margin-bottom: 40px;
}

.couponslider-coupon {
  padding: 90px 62px 10px;
}

.couponslider-coupon svg {
  margin-bottom: 25px;
}

.couponslider-tip {
  margin-top: 10px;
}

.couponslider-input {
  display: inline-block;
  vertical-align: middle;
  width: 340px;
  height: 40px;
  font-size: 16px;
  box-sizing: border-box;
  outline: none;
  margin-right: 10px;
  padding: 0 16px;
  border: 1px solid #999999;
}

.couponslider-input-code {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  letter-spacing: 2px;
  background: #DAD6D6;
}

.couponslider-button {
  display: inline-block;
  vertical-align: middle;
  width: 150px;
  height: 40px;
  font-size: 16px;
  color: #fff;
  background: #2D2D2D;
  outline: none;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
}

.couponslider-content-hide {
  display: none;
}

.popuptip {
  width: 300px;
  min-height: 80px;
  background: #12cea4;
  border-radius: 10px;
  position: fixed;
  right: 0;
  top: 10%;
  z-index: 99;
  text-align: center;
  padding: 20px 25px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: right .8s;
}

.popuptip-hide {
  right: -100%;
}

.popuptip-close {
  font-size: 30px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  display: block;
  right: 0;
  top: 0;
  cursor: pointer;
  color: #fff;
}

@media (max-width: 1025px) {
  .couponslider-left, .couponslider-right {
    height: 230px;
  }
  .couponslider-left {
    width: 32px;
  }
  .couponslider-right {
    width: 400px;
  }
  .couponslider-box {
    width: 432px;
  }
  .couponslider-coupon {
    padding: 50px 40px 10px;
  }
  .couponslider-coupon svg {
    margin-bottom: 15px;
  }
  .couponslider-input {
    width: 200px;
    height: 30px;
    font-size: 14px;
    padding: 0 10px;
  }
  .couponslider-button {
    width: 100px;
    height: 30px;
    font-size: 14px;
  }
  .couponslider-coupon .couponslider-tip {
    margin-top: 4px;
    margin-bottom: 4px;
  }
  .couponslider-box-hide {
    right: -400px;
  }
  .couponslider-subscribe {
    padding: 10px 40px 10px;
  }
  .couponslider-subscribe svg {
    margin-bottom: 10px;
  }
}

@media (max-width: 767px) {
  .couponslider-left, .couponslider-right {
    height: 170px;
  }
  .couponslider-left {
    width: 24px;
  }
  .couponslider-right {
    width: 300px;
  }
  .couponslider-box {
    width: 324px;
  }
  .couponslider-box-hide {
    right: -300px;
  }
  .couponslider-subscribe {
    padding: 0px 10px 10px;
  }
  .couponslider-subscribe svg {
    margin-bottom: -10px;
    margin-top: -10px;
  }
  .couponslider-subscribe .couponslider-tip {
    margin-top: 8px;
  }
  .couponslider-input {
    width: 170px;
  }
  .couponslider-coupon {
    padding: 25px 10px 10px;
  }
  .couponslider-coupon svg {
    margin-bottom: 6px;
  }
  .popuptip {
    width: auto;
    min-width: 168px;
    max-width: 100%;
  }
  .popuptip-close {
    font-size: 20px;
    width: 30px;
    height: 30px;
    line-height: 30px;
  }
}
